import { useScreenStore, useSlidesStore } from '@/store'
import { enterFullscreen, exitFullscreen, isFullscreen } from '@/utils/fullscreen'

export default () => {
  const screenStore = useScreenStore()
  const slidesStore = useSlidesStore()

  // 
  const enterScreening = () => {
    // enterFullscreen()
    // iframe
    window.parent?.postMessage({type: 'ppt', action: 'openFullScreen'}, '*')
    screenStore.setScreening(true)
  }

  // 
  const enterScreeningFromStart = () => {
    slidesStore.updateSlideIndex(0)
    enterScreening()
  }

  // 
  const exitScreening = () => {
    // iframe
    window.parent?.postMessage({type: 'ppt', action: 'closeFullScreen'}, '*')
    screenStore.setScreening(false)
    // if (isFullscreen()) exitFullscreen()
  }

  return {
    enterScreening,
    enterScreeningFromStart,
    exitScreening,
  }
}